import { BrowserModule } from '@angular/platform-browser';
import {isDevMode, NgModule} from '@angular/core';
import { AppComponent } from './app.component';
import {RouterModule, Routes} from '@angular/router';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {HttpClientModule} from "@angular/common/http";
import {DevToolsExtension, NgRedux, NgReduxModule} from "@angular-redux/store";
import {StoreModule} from '@ngrx/store';
import {AuthGuard} from "./auth-module/authguard.service";
import {DatePipe} from "@angular/common";


const routes: Routes = [
  {
    path: '', redirectTo: 'login', pathMatch: 'full'
  },
  {
    path: 'login',  loadChildren: '../app/auth-module/auth.module#AuthModule'
  },
  {
    path: 'home', canActivate: [AuthGuard], loadChildren: '../app/home-layout/home.layout.module#HomeLayoutModule'
  }
];


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    NgReduxModule,
    RouterModule.forRoot(routes,{useHash:true}),
    StoreModule.forRoot({
      /* an empty object here for this time */
    }),
  ],
  providers:[DatePipe],
  bootstrap: [AppComponent]
})
export class AppModule {

  constructor() {
  }

}
