import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {fromAuthentication} from "./auth-store/auth.reducer";
import {Store} from "@ngrx/store";

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {


  constructor( private _router: Router,   private store: Store<fromAuthentication.AuthenticationState>) {
  }

  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    let status =  false;
     this.store
     .select<any>((state: any) => state.authentication)
     .subscribe((loginInfo: any) => {
         if (loginInfo && typeof loginInfo !== 'undefined' && Object.keys(loginInfo).length > 0) {
           status =  true;
         } else {
           // navigate to login page
           this._router.navigate(['/login']);
           // you can save redirect url so after authing we can move them back to the page they requested
           status =  false;
         }
     }
     );

     return status;


  }

}
